import React from "react";
import Image from "./Image";

interface LogoProps {
  variant?: "full" | "icon" | "text" | "stacked";
  size?: "xs" | "sm" | "md" | "lg" | "xl";
  theme?: "light" | "dark" | "auto";
  src?: string;
  fallbackSrc?: string;
  className?: string;
  onClick?: () => void;
}

interface LogoTextProps {
  variant?: "full" | "short";
  size?: "xs" | "sm" | "md" | "lg" | "xl";
  theme?: "light" | "dark" | "auto";
  className?: string;
  onClick?: () => void;
}

const Logo: React.FC<LogoProps> & {
  Text: React.FC<LogoTextProps>;
} = ({
  variant = "full",
  size = "md",
  theme = "auto",
  src,
  fallbackSrc,
  className = "",
  onClick,
}) => {
  const sizeClasses = {
    xs: "h-6 w-auto",
    sm: "h-8 w-auto",
    md: "h-10 w-auto",
    lg: "h-12 w-auto",
    xl: "h-16 w-auto",
  };

  const containerClasses = `inline-flex items-center ${onClick ? "cursor-pointer hover:opacity-80 transition-opacity" : ""} ${className}`;

  // Generate placeholder logo based on variant
  const generatePlaceholderLogo = () => {
    const baseClasses = "flex items-center justify-center font-bold";
    const bgClasses =
      theme === "light"
        ? "bg-primary-400"
        : theme === "dark"
          ? "bg-primary-300"
          : "bg-primary-400 dark:bg-primary-300";
    const textClasses =
      theme === "light"
        ? "text-black"
        : theme === "dark"
          ? "text-gray-200"
          : "bg-primary-400 dark:text-black";

    const sizeStyles = {
      xs: { container: "h-6 px-2", text: "text-xs" },
      sm: { container: "h-8 px-3", text: "text-sm" },
      md: { container: "h-10 px-4", text: "text-base" },
      lg: { container: "h-12 px-5", text: "text-lg" },
      xl: { container: "h-16 px-6", text: "text-xl" },
    };

    const currentSize = sizeStyles[size];

    if (variant === "icon") {
      return (
        <div
          className={`${baseClasses} ${bgClasses} ${textClasses} ${currentSize.container} ${currentSize.text} aspect-square rounded-lg`}
        >
          CA
        </div>
      );
    }

    if (variant === "text") {
      return (
        <div className={`${baseClasses} ${textClasses} ${currentSize.text}`}>
          Ceralume
        </div>
      );
    }

    if (variant === "stacked") {
      return (
        <div className="flex flex-col items-center">
          <div
            className={`${baseClasses} ${bgClasses} ${textClasses} ${sizeStyles.sm.container} ${sizeStyles.sm.text} aspect-square rounded-lg mb-1`}
          >
            CA
          </div>
          <div className={`${textClasses} text-xs font-medium`}>Ceralume</div>
        </div>
      );
    }

    // full variant - icon + text
    return (
      <div className="flex items-center space-x-2">
        <div
          className={`${baseClasses} ${bgClasses} ${textClasses} ${currentSize.container} ${currentSize.text} aspect-square rounded-lg`}
        >
          CA
        </div>
        <div className={`${textClasses} ${currentSize.text} font-bold`}>
          Ceralume
        </div>
      </div>
    );
  };

  if (src) {
    return (
      <div className={containerClasses} onClick={onClick}>
        <Image
          src={src}
          {...(fallbackSrc && { fallbackSrc })}
          alt="Company Logo"
          className={sizeClasses[size]}
          placeholder="skeleton"
          aspectRatio="auto"
          objectFit="contain"
        />
      </div>
    );
  }

  return (
    <div className={containerClasses} onClick={onClick}>
      {generatePlaceholderLogo()}
    </div>
  );
};

const LogoText: React.FC<LogoTextProps> = ({
  variant = "full",
  size = "md",
  theme = "auto",
  className = "",
  onClick,
}) => {
  const sizeClasses = {
    xs: "text-xs",
    sm: "text-sm",
    md: "text-base",
    lg: "text-lg",
    xl: "text-xl",
  };

  const themeClasses =
    theme === "light"
      ? "text-gray-900"
      : theme === "dark"
        ? "text-gray-100"
        : "text-gray-900 dark:text-gray-100";

  const containerClasses = `font-bold ${sizeClasses[size]} ${themeClasses} ${onClick ? "cursor-pointer hover:opacity-80 transition-opacity" : ""} ${className}`;

  const text = variant === "short" ? "Ceralume" : "Ceralume UI Demo";

  return (
    <span className={containerClasses} onClick={onClick}>
      {text}
    </span>
  );
};

Logo.Text = LogoText;

export default Logo;
